import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { LegendComponent } from './legend';
import { LegendItemComponent } from './legend';
import { LineIndexedChartComponent } from './line-indexed-chart';
import { PieChartComponent } from './pie-chart';
import { StackedBarCategoryRotatedChartComponent } from './stacked-bar-category-rotated-chart';
import { StackedBarTimeseriesChartComponent } from './stacked-bar-timeseries-chart';
import { LineTimeseriesChartComponent } from './line-timeseries-chart';

@NgModule({
  declarations: [
    LegendComponent,
    LegendItemComponent,
    LineIndexedChartComponent,
    PieChartComponent,
    StackedBarCategoryRotatedChartComponent,
    StackedBarTimeseriesChartComponent,
    LineTimeseriesChartComponent
  ],
  exports: [
    LineIndexedChartComponent,
    PieChartComponent,
    StackedBarCategoryRotatedChartComponent,
    StackedBarTimeseriesChartComponent,
    LineTimeseriesChartComponent
  ],
  imports: [
    CommonModule
  ],
  providers: []
})

export class ChartsModule { }



